.error__msg__container {
  background-color: #332852;
  margin-top: 4.6em;
  display: grid;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 8em 1em;
  color: #b12f6e;
}
.error__msg__container h1 {
  font-size: 9em;
  line-height: 1em;
}
.error__msg__container p {
  line-height: 1.2;
  font-size: 1.1em;
  margin-bottom: 1.5em;
}
.error__msg__container a {
  justify-self: center;
  background-color: #e2a60e;
  text-decoration: none;
  display: inline-block;
  padding: 0.6em 1em;
  border-radius: 4px;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.3);
  font-size: 1.1em;
  color: #1a1d23;
  font-weight: 600;
}